.section {
    position: relative;
    background-repeat: no-repeat;
    background-position: 50% 50%;
    background-size: cover;
}

.sectionParapet {
    position: absolute;
    z-index: 1;
    bottom: -56px;
    left: 0;
    width: 100%;
    height: 133px;
    background-repeat: repeat-x;
    background-image: url('../../assets/images/pg.png');
}

.sectionInner {
    position: relative;
    padding-bottom: 62px;
}

.sectionInner::after,
.sectionInner::before {
    content: '';
    position: absolute;
    width: 88px;
    background-image: url("../../assets/images/brick_l.png");
    background-repeat: repeat-y;
    top: 0;
    left: 0;
    bottom: 0;
}

.sectionInner::before {
    background-image: url("../../assets/images/brick_l.png");
}

.sectionInner::after {
    background-image: url("../../assets/images/brick_r.png");
    background-repeat: repeat-y;
    top: 0;
    left: auto;
    right: 0;
    bottom: 0;
}

.section :global(.panel:nth-child(odd)) {
    padding-top: 40px;
}

.section :global(.panel:nth-child(even)) {
    padding-bottom: 40px;
}

.section :global(.panel:nth-child(even)) .window {
    margin-left: 480px;
}

.section :global(.panel:nth-child(even)) .person {
    order: -1;
    margin-left: 0;
}

.section:nth-child(3n+1) {
    background-image: url('../../assets/images/background_gray.jpg');
}

.section:nth-child(3n+2) {
    background-image: url('../../assets/images/background_rose.jpg');
}

.section:nth-child(3n+3) {
    background-image: url('../../assets/images/background_yellow.jpg');
}

.root :global(.panel) {
    display: flex;
    justify-content: center;
    align-content: center;
    position: static;
    width: 100%;
    height: 560px;
    text-align: center;
}

.root :global(.panel-first),
.root :global(.panel-last) {
    align-items: center;
    height: 100vh;
    min-height: 560px;
}

.window {
    position: relative;
    min-width: 480px;
    height: 560px;
    background-image: url("../../assets/images/window.png");
    background-repeat: no-repeat;
    background-size: cover;
    background-position: 50% 50%;
}

.windowPreviewVideo {
    position: relative;
    top: 92px;
    left: 115px;
    width: 240px;
    height: 300px;
    opacity: 0;
    overflow: hidden;
    transition: all 1.5s ease-out;
}

.windowPreviewVideoShow{
    opacity: 1;
    transition: all 1.5s ease-in 0.2s;
}

.windowPreviewVideo video {
    position: absolute;
    right: 0;
    bottom: 0;
    transform: translateX(calc((100% - 240px) / 2)) translateY(calc((100% - 300px) / 2));
}

.person {
    position: relative;
    top: 12px;
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
    align-content: center;
    width: 480px;
    height: 560px;
    margin-left: 480px;
    opacity: 0.2;
    transition: opacity 1s ease-out, top 0.4s ease-out;
}

.personShow {
    top: 0;
    opacity: 1;
    transition: opacity 1s ease-in 0.2s, top 0.4s ease-in 0.1s;
}

.personTitle {
    font-family: var(--font-lato);
    text-transform: uppercase;
    color: var(--c-white);
    margin-bottom: 28px;
}

.personName {
    font-size: 21px;
    font-weight: 500;
    line-height: 1;
    margin-bottom: 20px;
}

.personSurname {
    font-size: 72px;
    font-weight: 900;
    line-height: 1;
    color: var(--c-black)
}

.personPosition {
    width: 90%;
    margin: 0 auto 24px;
    font-family: var(--font-lato);
    font-size: 14px;
    font-weight: 900;
    line-height: 22px;
    text-transform: uppercase;
    color: var(--c-white);
}

.frame {
    position: fixed;
    z-index: 10;
    width: 100%;
    height: 100vh;
    top: 0;
    left: 0;
    pointer-events: none;
}

.frame::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 440px;
    background: linear-gradient(180deg, rgba(46,26,36,1) 0%, rgba(69,41,59,0.7) 30%, rgba(101,56,49,0.4) 60%, rgba(101,56,49,0) 100%);
}

.floor {
    width: 250px;
    height: 492px;
    position: relative;
    top: 50%;
    left: -125px;
    margin-left: 50%;
    margin-top: -246px;
    pointer-events: none;
}

.floorRound,
.floorRound svg {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
}

.floorValue {
    color: var(--c-white);
    font-family: var(--font-aesthet-nova);
    line-height: 1;
}

.floorCurrent {
    font-size: 90px;
    top: 50%;
    position: absolute;
    margin-left: 50%;
    width: 60px;
    text-align: center;
    left: -30px;
    margin-top: -47px;
}

.floorPrev,
.floorNext {
    width: 60px;
    position: absolute;
    left: -29px;
    margin-left: 50%;
    font-size: 24px;
    text-align: center;
}

.floorPrev {
    top: 60px;
}

.floorNext {
    top: 100%;
    margin-top: -84px;
}

.lineBox {
     position: relative;
     display: flex;
     align-items: center;
     align-content: center;
 }

.lineBox::before,
.lineBox::after {
    display: block;
    content: '';
    width: 220px;
    height: 2px;
    background-color: var(--c-white);
}

.lineBoxDot {
    position: relative;
    width: 20px;
    height: 21px;
    margin: 0 10px;
}

.lineBoxDot svg {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
}

.articleTitle {
    color: var(--c-white);
    font-size: 34px;
    font-family: var(--font-lato);
    font-weight: 300;
}

.articleTitleText {
    padding: 12px 0 30px;
}

.articleTitle::after {
    content:'';
    display: block;
    width: 100%;
    height: 2px;
    background-color: var(--c-white);
}

.modal :global(.ant-modal-content) {
    border-radius: initial;
}

.modal :global(.ant-modal-close) {
    display: none;
}

.borderLayer {
    position: fixed;
    z-index: 20;
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh;
    pointer-events: none;
}

.borderLayerLine {
    position: absolute;
    background-color: var(--c-white);
}

.lineTop {
    height: 2px;
    top: 4vh;
    left: 2vw;
    right: 2vw;
    margin-left: 30px;
    margin-right: 30px;
}

.lineBottom {
    height: 2px;
    bottom: 4vh;
    left: 2vw;
    right: 2vw;
    margin-left: 30px;
    margin-right: 30px;
}

.lineLeft {
    left: 2vw;
    top: 4vh;
    bottom: 4vh;
    width: 2px;
    margin-top: 30px;
    margin-bottom: 30px;
}

.lineRight {
    right: 2vw;
    top: 4vh;
    bottom: 4vh;
    width: 2px;
    margin-top: 30px;
    margin-bottom: 30px;
}

.corner {
    position: absolute;
    width: 30px;
    height: 30px;
}

.cornerTop {
    top: 4vh;
}

.cornerLeft {
    left: 2vw;
}

.cornerRight {
    transform: rotate(90deg);
    right: 2vw;
}

.cornerBottom {
    bottom: 4vh;
    transform: rotate(180deg);
}

.cornerLeft.cornerBottom {
    transform: rotate(270deg);
}

.modal :global(.ant-modal-content::before) {
    content: '';
    position: absolute;
    z-index: -1;
    width: 50%;
    top: calc(4vh + 29px);
    bottom: calc(4vh + 29px);
    left: 0;
    background-color: var(--c-white);
}

.modal :global(.ant-modal-content::after) {
     content: '';
     position: absolute;
     z-index: -1;
     width: 50%;
     top: calc(4vh + 29px);
     bottom: calc(4vh + 29px);
     right: 0;
     background-color: var(--c-white);
}

.modal :global(.ant-modal-body::after) {
    content: '';
    position: absolute;
    z-index: -1;
    width: calc(100% - 60px);
    top: 4vh;
    bottom: 4vh;
    left: 30px;
    background-color: var(--c-white);
}

.modalInner {
    position: absolute;
    top: calc(4vh + 30px);
    left: 2vw;
    bottom: calc(4vh + 30px);
    right: 2vw;
    background-color: var(--c-white);
    overflow: hidden;
}

.detailVideoWrapper {
    position: relative;
    height: 0;
    padding-bottom: 56.25%;
    top: 50%;
    transform: translateY(-50%)
}

.detailVideo {
    position: absolute;
    top: 50%;
    left: 0;
    width: 100%;
    transform: translateY(-50%)
}

.modalCorner {
    position: absolute;
    width: 30px;
    height: 30px;
}

.modalCorner svg {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
}

.modalCornerTop {
     top: 4vh
}

.modalCornerBottom {
    bottom: 4vh;
    transform: rotate(270deg);
}

.modalCornerLeft {
    left: 0
}

.modalCornerRight {
    top: 4vh;
    right: 0;
    transform: rotate(90deg);
}

.modalCornerRight.modalCornerBottom {
    top: auto;
    transform: rotate(180deg);
}

.modal :global(.ant-modal-content) {
    background: none;
}