.root {
    font-family: var(--font-lato);
    font-size: 24px;
    font-weight: 300;
    padding-top: 30px;
    padding-bottom: 30px;
}

.lineBox {
    margin: 0 auto;
    position: relative;
    display: flex;
    align-items: center;
    align-content: center;
    justify-content: center;
}

.lineBox::before,
.lineBox::after {
    display: block;
    content: '';
    max-width: 430px;
    width: 100%;
    height: 2px;
    background-color: var(--c-black);
}

.lineBoxDot {
    position: relative;
    width: 20px;
    height: 21px;
    margin: 0 10px;
}

.lineBoxDot svg {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
}

.lineBoxDot svg rect {
    fill: var(--c-black);
}

.personInfoHeader {
    padding: 30px 0 0;
}

.personInfoTitle {
    display: flex;
    align-items: baseline;
    justify-content: center;
    text-transform: uppercase;
    margin-bottom: 18px;
}

.personInfoName {
    line-height: 1;
}

.personInfoSurname {
    text-transform: uppercase;
    font-weight: 900;
    margin-left: 6px;
    line-height: 1;
}

.personInfoPosition {
    font-size: 14px;
    text-transform: uppercase;
    text-align: center;
    font-weight: 600;
    margin-bottom: 54px;
}

.personInfoDescription {
    position: relative;
    font-size: 38px;
    font-weight: 300;
    text-align: center;
    max-width: 900px;
    margin: 0 auto 76px;
    padding-top: 62px;
    padding-bottom: 40px;
    line-height: 44px;
}

.personInfoDescription::after {
    content: '';
    position: absolute;
    top: 100%;
    width: 100%;
    max-width: 500px;
    transform: translateX(-50%);
    height: 2px;
    background-color: var(--c-black);
}

.personInfoArticle {

}

.personInfoArticleTitle {
    font-size: 24px;
    font-weight: 900;
    text-align: center;
    margin-bottom: 71px;
}

.personInfoArticleContent {
    font-size: 21px;
    font-weight: 400;
    max-width: 900px;
    margin: 0 auto;
}

.personInfoArticleContent p {
    margin: 0;
    margin-top: 40px;
}

.personInfoArticleContent p:first-child {
    margin-top: 0;
}