.root {
    position: relative;
    display: flex;
    justify-content: center;
    align-content: center;
    align-items: center;
    /*position: fixed;*/
    width: 100%;
    min-height: 100vh;
/*    height: 100vh;*/

    overflow: hidden;
}

.root::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 440px;
    background: linear-gradient(180deg, rgba(46,26,36,1) 0%, rgba(69,41,59,0.7) 30%, rgba(101,56,49,0.4) 60%, rgba(101,56,49,0) 100%);
}

.rootInner {
    padding-top: 10px;
   /* position: relative;*/
    z-index: 10;
    color: var(--c-white);
    pointer-events: none;
}

.ribbonHotel {
    position: relative;
    width: 610px;
    height: 165px;
}

.ribbonHotel svg {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
}

.ribbonYear {
    display: flex;
    justify-content: center;
    align-items: center;
    font-family: var(--font-aesthet-nova);
    line-height: 1;
    margin-bottom: 80px;
}

.ribbonYearValue {
    font-size: 350px;
    font-weight: 900;
}

.ribbonYearText {
    font-size: 34px;
    font-weight: 400;
    text-transform: uppercase;
}

.ribbonEnter {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-content: center;
    align-items: center;
    font-size: 64px;
    font-family: var(--font-aesthet-nova);
    font-weight: 700;
    text-transform: uppercase;
    margin-bottom: 36px;
    pointer-events: auto;
}

.ribbonEnterText {
    cursor: pointer;
    margin-bottom: 36px;
}

.lineBox {
    position: relative;
    display: flex;
    align-items: center;
    align-content: center;
}

.lineBox::before,
.lineBox::after {
    display: block;
    content: '';
    width: 210px;
    height: 2px;
    background-color: var(--c-white);
}

.lineBoxDot {
    position: relative;
    width: 20px;
    height: 21px;
    margin: 0 10px;
}

.lineBoxDot svg {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
}

.gotoRG {
    display: flex;
    justify-content: center;
    font-size: 18px;
    font-family: var(--font-aesthet-nova);
    font-weight: 400;
    text-transform: uppercase;
    margin-bottom: 10px;
    pointer-events: auto;
}

.gotoRG a {
    cursor: pointer;
    color: var(--c-white);
    text-decoration: none;
}

.borderLayer {
    position: fixed;
    z-index: 20;
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh;
    pointer-events: none;
}

.borderLayerLine {
    position: absolute;
    background-color: var(--c-white);
}

.lineTop {
    height: 2px;
    top: 4vh;
    left: 2vw;
    right: 2vw;
    margin-left: 30px;
    margin-right: 30px;
}

.lineBottom {
    height: 2px;
    bottom: 4vh;
    left: 2vw;
    right: 2vw;
    margin-left: 30px;
    margin-right: 30px;
}

.lineLeft {
    left: 2vw;
    top: 4vh;
    bottom: 4vh;
    width: 2px;
    margin-top: 30px;
    margin-bottom: 30px;
}

.lineRight {
    right: 2vw;
    top: 4vh;
    bottom: 4vh;
    width: 2px;
    margin-top: 30px;
    margin-bottom: 30px;
}

.man {
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    overflow: hidden;
    background-position: 50% 50%;
    background-repeat: no-repeat;
    background-image: url("../../assets/images/hotel.png");
    background-size: cover;
}

.manTable {
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 14.083333333333334%; /* height: 169px */
    background-image: url("../../assets/images/table.png");
    background-position: 0 0;
    background-repeat: repeat-x;
    background-color: #b89e9d;
    background-size: contain;
}

.manPicture {
    position: absolute;
    left: 25px;
    bottom: 0;
    margin-left: 50%;
    height: 72.75%;
}

.corner {
    position: absolute;
    width: 30px;
    height: 30px;
}

.cornerTop {
    top: 4vh;
}

.cornerLeft {
    left: 2vw;
}

.cornerRight {
    transform: rotate(90deg);
    right: 2vw;
}

.cornerBottom {
    bottom: 4vh;
    transform: rotate(180deg);
}

.cornerLeft.cornerBottom {
    transform: rotate(270deg);
}